@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@400;600;700;900&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Titillium Web", sans-serif;
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 6rem;
}

.app {
  width: 100%;
  height: 100%;
  position: relative;
  background: url("./assets/background2.jpg") no-repeat center center/cover;
  color: rgb(255, 255, 255);
}

.app .search {
  text-align: center;
  padding: 1rem;
}
.app input {
  padding: 0.7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgb(96, 96, 96);
  background: rgb(155, 153, 153, 0.5);
  color: rgb(255, 255, 255);
}
::placeholder {
  color: rgb(255, 255, 255);
}

.container {
  max-width: 700px;
  height: 700px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.app .top {
  width: 100%;
  margin: 1rem auto;
}

.app .description {
  margin-top: 30%;
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269deg);
}

.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 5rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(30, 30, 30, 0.5);
}

.bold {
  font-weight: 700;
}
